:root {
    --event-card-main-color: #f8c456;
}

.card-outer-container * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.card-outer-container {
    padding: 5px;
    background-color: var(--event-card-main-color);


box-shadow: 0 0 50px -20px #f8e856;
    /* width: 500px; */
    margin: 0;
}
.card-inner-container {
    position: relative;
    margin: 0;

    background-color: #0d0e10;
    /* width: 100%; */
    /* height: 100%; */
    color: var(--event-card-main-color);

    /* outline: var(--event-card-main-color);
  outline-width: 5px;
  outline-style: solid; */
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    text-align: left;
    padding: 1rem;
    padding-right: 2rem;
    padding-bottom: 2rem;
}
.main-container {
    position: relative;
}
.event-name {
    font-size: 2.5rem;
    margin-bottom: 10px;
}
.event-tagline {
    font-size: 1rem;
    font-weight: normal;
    margin-bottom: 10px;
}
.more-button {
    position: absolute;
    right: 10px;
    bottom: 0rem;
    clip-path: polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%);
    padding: 5px 20px 1px 20px;
    background-color: #f8c456;
    color: #0d0e10;
    font-size: 12.5px;
    text-decoration: none;
}
@media (max-width: 600px) {
    .event-name {
        font-size: 1.75rem;
    }
    .event-tagline {
        font-size: 0.7rem;
    }

    .more-button {
        font-size: 10px;
    }
}

.live {
    color: aliceblue;
    box-shadow:0 0 15px 0 red;
    background-color: red;
    padding: 5px 10px;
    margin: 5px;
    font-weight: bold;
    /* font-family: "Android 101"; */
    border-radius: 25px;
    /*margin-top: 25px;
  */ /*width: 50px;
  */
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -ms-border-radius: 25px;
    -o-border-radius: 25px;
}
